@import '../../../../styles/sass/main.scss';

.box {
  background-color: $white;
  border-radius: 95px;
  display: flex;
  flex-direction: column;
  padding: 60px;
  cursor: auto;

  h1 {
    font-size: $subtitle;
    font-weight: 600;
    line-height: 0;
  }

  p {
    font-size: $text;
    font-weight: 300;
    margin-bottom: 60px;
  }

  div {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2, 0fr);
    justify-content: end;
    grid-gap: 20px;

    .btn {
      width: 150px;
    }

    .btnDelete {
      color: $white;
      background-color: $pink-300;
      border: solid 2px $pink-300;
      width: 230px;
    }
  }
}
